import React, {useState, useEffect} from 'react';
import {connect} from 'react-redux';
import HeroComponents from "components/layout-components/CustomrPageHeaderAlt";
import ButtonPermission from "components/permission-components/button.permission";
import {SaveOutlined} from "@ant-design/icons";
import ContainerTailwind from "components/layout-components/ContainerTailwind";
import {Avatar, Card, Col, DatePicker, Form, Input, message, Row, Select} from "antd";
import AirlinesSelectForm from "../../../../../components/forms/airlines.select.form";
import CitySelectForm from "../../../../../components/forms/city-select.form";
import TypeAnnouncementSelectForm from "../../../../../components/forms/type-announcement.select.form";
import BoardingGateSelectForm from "../../../../../components/forms/boarding-gate.select.form";
import FloorSelectForm from "../../../../../components/forms/floor.select.form";
import TypeStatusAirlinesForm from "../../../../../components/forms/type-status-airlines.form";
import ApiService from "../../../../../../../services/api.service";
import moment from "moment";
import {findKey, SearchLikeArr} from "../../../../../../../utils";
import Flex from "../../../../../../../components/shared-components/Flex";

function mapStateToProps(state) {
	return {};
}

function Index() {
	const [loading, setLoading] = useState(false)
	
	const [airline, setAirline] = useState(null)
	
	const [form] = Form.useForm();
	
	const onFinish = () => {
		setLoading(true)
		form.validateFields()
			.then((value) => {
				Reflect.set(value, 'schedule_time', moment(value?.schedule_time).isValid() ? moment(value?.schedule_time).format("HH:mm") : moment(value?.schedule_time))
				new ApiService({
					url: `/api/v1/flight/schedule`,
					mode: "express",
					body: value
				}).post()
					.then((response) => {
						if (!response?.error) {
							message.success(response?.message)
						} else {
							message.info(response?.message)
						}
						setLoading(false)
					})
					.catch((err) => {
						message.error(err?.message)
						setLoading(false)
					})
			})
			.catch((err) => {
				setLoading(false)
			})
	}
	
	return (
		<>
			<HeroComponents
				loading={false}
				name={"Create New Schedule"}
				positionName={'Display | Schedule Create'}
				thumbnail={null}
				positionColor={'cyan'}
				extra={<>
					<ButtonPermission
						permission={['admin']}
						theme={{
							type: "notification",
							property: {
								message: "Access Denied",
								description: "Anda tidak dapat menyimpan ke database "
							}
						}}
						config={{
							icon: <SaveOutlined/>,
							type: "primary",
							danger: false,
							loading,
							ghost: true,
							size: "small",
							onClick: function () {
								onFinish();
							}
						}}>
						<span>Save</span>
					</ButtonPermission>
				</>}
			/>
			
			<div style={{marginTop: 90}}>
				<ContainerTailwind>
					<Form
						form={form}
						layout={'vertical'}
					>
						<Row gutter={24}>
							<Col xs={24} lg={16}>
								<Card bordered={false}>
									<Row gutter={24}>
										<Col span={8}>
											<Form.Item hasFeedback name={'day'} label={'Day'}
											           rules={[{required: true, message: "Can't be empty"}]}>
												<Select placeholder={'select one day'}>
													{[
														"Sunday",
														"Monday",
														"Tuesday",
														"Wednesday",
														"Thursday",
														"Friday",
														"Saturday"
													].map((item) => {
														return <Select.Option key={item} value={item}>{item}</Select.Option>
													})}
												
												</Select>
											</Form.Item>
										</Col>
										<Col span={8}>
											<TypeStatusAirlinesForm
												name={'type'}
												label={'Type'}
												keyValue={['data', 'identifier']}
												keyDisplay={['data', 'name']}
												select={{
													showSearch: true
												}}
												mapping={function ({data}) {
													let ARR = SearchLikeArr(data, ['data', 'name'], 'ARR')
													let DEPA = SearchLikeArr(data, ['data', 'name'], 'DEPA')
													return (
														[
															...ARR,
															...DEPA
														].map((item, index) => (
															<Select.Option
																value={findKey(item, ['identifier']) ?? findKey(item, ['data', 'name']) ?? item?.id}
																key={item?.id}>{findKey(item, ['data', 'name']) ?? "undefined"}</Select.Option>
														))
													)
												}}
												rules={[
													{
														required: true,
														message: "Can't be empty!"
													}
												]}
											/>
										</Col>
										<Col span={8}>
											<AirlinesSelectForm
												name={'airline_id'}
												label={'Airlines'}
												keyValue={['id']}
												keyDisplay={['data', 'name']}
												select={{
													onChange: function (val, args) {
														setAirline(args)
														form.setFieldsValue({
															airline_id: val
														})
													},
													placeholder: "Select Airlines"
												}}
												rules={[
													{
														required: true,
														message: "Can't be empty!"
													}
												]}
											>
												{function ({item, index}) {
													
													return (
														<Select.Option value={item?.id} key={`airlines-select-${index}`} {...item}>
															<Flex alignItems={'center'} className={'h-full mk-gap-2'}>
																<Avatar src={[process.env.REACT_APP_API_MASTER, item?.data?.image?.dark].join('')}
																        size={30}/>
																<span>{[findKey(item, ['data', 'name']), `(${findKey(item, ['data', 'code'])})` ?? ""].join(" ")}</span>
															</Flex>
														</Select.Option>
													)
												}}
											</AirlinesSelectForm>
										</Col>
										<Col span={12}>
											<Form.Item
												hasFeedback
												name={'flight_no'}
												label={'Flight Number'}
												rules={[
													{
														required: true,
														message: "Can't be empty!"
													},
													({getFieldValue}) => ({
														validator(rule, value) {
															if (getFieldValue('airline_id')) {
																return Promise.resolve();
															}
															return Promise.reject('please select the airline first!');
														},
													})
												]}
											>
												<Input
													addonBefore={airline ? airline?.data?.code ?? "-" : '-'}
													placeholder={'Input your flight number'}
													autoComplete={'off'}
												/>
											</Form.Item>
										</Col>
										<Col span={12}>
											<CitySelectForm
												name={'origin_id'}
												label={'To / Destination'}
												select={{
													placeholder: "Select your To/Destination"
												}}
												rules={[
													{
														required: true,
														message: "Can't be empty!"
													}
												]}
											/>
										</Col>
										<Col span={12}>
											<Form.Item
												hasFeedback
												name={'schedule_time'}
												label={"Schedule Time"}
												rules={[
													{
														required: true,
														message: "Can't be empty!"
													}
												]}>
												<DatePicker.TimePicker
													format={'HH:mm'}
													style={{
														width: "100%"
													}}
												/>
											</Form.Item>
										</Col>
									</Row>
								
								
								</Card>
							</Col>
							<Col xs={24} lg={8}>
								<Card bordered={false}>
									<Form.Item
										hasFeedback
										name={'remarks'} label={'Remarks'} rules={[{required: false, message: "Can't be empty!"}]}>
										<Input.TextArea
											allowClear
											showCount={true}
											autoSize={{minRows: 4, maxRows: 6}}
											size={"small"}
										/>
									</Form.Item>
								</Card>
							</Col>
						</Row>
					</Form>
				</ContainerTailwind>
			</div>
		</>
	);
}

export default connect(
	mapStateToProps,
)(Index);